// @require '../../style/reset.scss'

import React from 'react';
import ReactDom from 'react-dom';
import Datepicker from '../index';

const dateNow = new Date();

const Demo = function () {
  return (
    <div
      style={{
        marginBottom: '280px'
      }}
    >
      <h2>Calendar Default Opened</h2>
      <Datepicker
        placeholder={'点击选择日期'}
        showPicker
        selected={dateNow}
      />
    </div>
  );
};

ReactDom.render(
  <Demo />,
  document.getElementById('demo4')
);
